<script>
import loading from "@/components/loading.vue"
export default {
    name: 'App',
    data: () => ({
        arreglo: [{ texto: "Titulo" }, { texto: "Titulo" }, { texto: "Titulo" },
        ],
        page: null,
        resultados: [],
        cargando: true,
    }),
    components: {
        loading,
    },
    methods: {
        buscar() {
            this.$store.state.loading = true
            this.$store.commit('cargar_datos')
        },
        cargar_resultados() {
            this.cargando=true
            this.resultados = this.$store.state.resultados
            this.axios.get("todos_usuarios").then((res) => {
                const usuarios = res.data
                for (var i = 0; i < this.resultados.length; i++) {
                    var user_post = usuarios.filter(u => u._id == this.resultados[i].refcreador)
                    console.log(user_post)
                    this.resultados[i].nombre = user_post[0].nombre_user
                }
                this.cargando = false
            })
        }
    },
    created() {
        this.cargar_resultados()
    },
    destroyed() {
        console.log("App eliminado");
    },
};
</script>
