                <v-row>
                    <v-img class="mt-2" max-height="80" max-width="50" src="@/assets/iconlogo.png"></v-img>
                    <h1 class="white--text mt-4 d-none d-lg-flex d-xl-none">UtalcaSearch</h1>
                </v-row>
            </v-btn>
            <v-spacer class="d-none d-sm-flex"></v-spacer>
            <v-container class="ml-1">
                <v-row justify="space-around">
                    <v-spacer class="d-none d-sm-flex"></v-spacer>
                    <v-flex xs6 sm5 md6>
                        <v-text-field class="mt-8" v-model="buscar_text" background-color="white" height="37" rounded
                            label="Buscar" single-line prepend-inner-icon="mdi-search-web" color="#0d1117"
                            @keyup.enter="buscar"></v-text-field>
                    </v-flex>
                    <v-spacer class="d-none d-sm-flex"></v-spacer>
                    <v-progress-circular class="mt-8" indeterminate color="primary"
