                    <div v-else>
                        <!-- No usuario registrado -->
                        <div class="mt-8" v-if="!this.$store.state.ingresoUsuario">
                            <v-btn color="white" depressed to="/login">
                                <v-icon dark>
                                    mdi-login-variant
                                </v-icon>
                            </v-btn>
                        </div>
                        <!-- Usuario registrado (Movil)-->
                        <div v-if="this.$store.state.ingresoUsuario" class="mt-7 d-flex d-sm-none">
                            <v-app-bar-nav-icon  style="color:#fff" @click.stop="drawerMovil = !drawerMovil"></v-app-bar-nav-icon>
                        </div>
                        <!-- Usuario registrado (Computador,Tablets)-->
                        <div class="mt-8 d-none d-sm-flex" v-if="this.$store.state.ingresoUsuario">
                            <v-row justify="center">
                                <v-menu bottom min-width="200px" rounded offset-y>
                                    <template v-slot:activator="{ on }">
                                        <v-btn icon x-large v-on="on">
                                            <v-avatar tile color="blue" rounded>
                                                <v-img :src="$store.state.imagenUsuario"></v-img>
                                            </v-avatar>
                                        </v-btn>
                                    </template>
                                    <v-card class="v_card_user white--text"
                                        style="background-color:rgba(59,130,246,.5);">
                                        <v-list-item-content class="justify-center">
                                            <div class="mx-auto text-center">
                                                <v-avatar color="brown">
                                                    <v-img :src="$store.state.imagenUsuario"></v-img>
                                                </v-avatar>
                                                <h3>{{ $store.state.nombreUsuario }}</h3>
                                                <v-row class="mt-3">
                                                    <v-spacer></v-spacer>
