            </v-container>
            <v-spacer></v-spacer>
        </v-app-bar>
    </div>
</template>
<script>
export default {
    name: 'App',
    data: () => ({
        buscar_text: "",
        resultados: [],
        imagen_user: "",
        nombre_user: "",
        drawerMovil: null,
    }),
    components: {
    },
    methods: {
        buscar() {
            this.$store.state.busqueda = this.buscar_text
            this.$store.state.loading = true
            this.$router.push("/search").catch(error => {
                if (error.name != "NavigationDuplicated") {
                    console.log("Navegacion Error")
                }
            });
            this.$store.commit('cargar_datos')
        },
        accion_card_user(opc) {
            if (opc == 1) {
                this.$router.push("/micuenta").catch(error => {
                    if (error.name != "NavigationDuplicated") {
                        console.log("Navegacion Error")
                    }
                });
            }
            else if (opc == 2) {
                this.$router.push("/").catch(error => {
                    if (error.name != "NavigationDuplicated") {
                        console.log("Navegacion Error")
                    }
                });
                localStorage.clear()
                this.$store.state.loading = true
                this.$store.commit('cargar_datos')
            }
        },
        redirigir_home() {
            this.$router.push("/").catch(error => {
                if (error.name != "NavigationDuplicated") {
                    console.log("Navegacion Error")
                }
            });
        }
    },
};
</script>
<style>
.v_card_user {
    background: url("@/assets/mic-drop.webp") no-repeat 0 0;
    background-size: 80px;
