<template>
    <v-app style="background-color: #151f32;">
        <loading v-if="this.$store.state.loading"></loading>
        <v-main v-else>
            <v-container fluid>
                <v-row align="center" class="mt-12">
                    <v-col class="text-center" cols="12">
                        <h1 class="text-h2 mb-4 white--text">
                            Utalca Search
                        </h1>
                        <h1 class="text-h6 mb-4 white--text">
                            Ingresar
                        </h1>
                    </v-col>
                </v-row>
                <div class="mt-5">
                    <v-card class="mx-auto white--text" max-width="500" outlined color="#484f58" height="320">
                        <v-card-text>
                            <h3 class="subheading white--text mb-5">Usuario</h3>
                            <v-text-field v-model="usuario" prepend-icon="mdi-account-circle" outlined solo-inverted
                                color="white" background-color=""></v-text-field>
                            <h3 class="subheading white--text mb-5">Contraseña</h3>
                            <v-text-field v-model="contrasena" color="white" prepend-icon="mdi-lock" outlined
                                solo-inverted :append-icon="mostrar ? 'mdi-eye' : 'mdi-eye-off'"
                                :type="mostrar ? 'text' : 'password'" name="input-10-1" counter
                                @click:append="mostrar = !mostrar"></v-text-field>
                            <v-card-actions class="my-n4">
                                <v-row>
                                    <v-btn class="white--text mt-4" color="green" small to="/crearcuenta">
                                        Crear cuenta</v-btn>
                                    <v-spacer></v-spacer>
                                    <v-btn :class="vBtn" class="white--text mt-4" color="green" small
                                        :disabled="vBtnIngreso" @click="ingreso()">
                                        Ingresar</v-btn>
                                </v-row>
                            </v-card-actions>
                        </v-card-text>
                    </v-card>
                </div>
            </v-container>
        </v-main>
    </v-app>
</template>
   
<script>
import Swal from 'sweetalert2'
import loading from "@/components/loading.vue"
export default {
    data() {
        return {
            usuario: "",
            contrasena: "",
            vBtnIngreso: true,
            usuarios: [],
            mostrar: null,
            nombre_ingreso: "Alumno",
        };
    },
    destroyed() {
        console.log("App eliminado");
    },
    beforeMount() {
        this.$store.state.loading = true
        this.$store.commit('cargar_datos')
    },
    components: {
        loading,
        Swal,
    },
    computed: {
        vBtn() {
            if (this.usuario == "" || this.contrasena == "") {
                this.vBtnIngreso = true
            } else {
                this.vBtnIngreso = false
            }
        },
    },
    methods: {
        ingreso() {
            this.axios.get("todos_usuarios").then((respsone) => {
                const usuarios = respsone.data
                var login_user = usuarios.filter(u => u.nombre_user == this.usuario && u.contrasena == this.contrasena)
                if (login_user.length !== 0) {
                    this.$store.state.ingresoUsuario=true
                    localStorage.setItem("key_user", login_user[0]._id)
                    if (login_user[0].roladmin) {
                        this.$router.push({ path: "/admin" })
                    } else{
                        this.$router.push({ path: "/micuenta" })
                    }
                    console.log("succssesfull")
                } else {
                    console.log("error")
                }
            })
        },
        crear() {

        }
    },
}
</script>