<script>
export default {
    name: 'mi_cuenta',
    data() {
        return {
            // default data
            items: [
                { title: 'Dashboard', icon: 'mdi-view-dashboard' },
                { title: 'Photos', icon: 'mdi-image' },
                { title: 'About', icon: 'mdi-help-box' },
            ],
            right: null,
            tab: null,
            titulo: null,
            descripcion: null,
            link: null,
            publicaciones:[],
            imagen_user:null,
            nombre_user:null,
        }
    },
    methods: {
        // default methods
        publicar(){
            this.$store.state.loading=true
            var publicacion = {
                refcreador: localStorage.getItem("key_user"),
                titulo:this.titulo,
                descripcion: this.descripcion,
                link: this.link,
                fecha: new Date().getFullYear() + "-" + new Date().getMonth(),
                likes:0,
            }
            this.axios.post("nuevo_publicacion",publicacion).then(()=>{
                this.$store.commit('cargar_datos')
            })
        },
        buscar_publicaciones(){
            this.axios.get("todos_publicaciones").then((resp)=>{
                const todos_publicaciones = resp.data
                this.publicaciones = todos_publicaciones.filter(p=> p.refcreador ===localStorage.getItem("key_user"))
                // mostrar por consola las publicaciones
                console.log(this.publicaciones)
            })
        }
    },
    created() {
        // default created
        this.buscar_publicaciones()
        this.nombre_user=this.$store.state.nombreUsuario
        this.imagen_user=this.$store.state.imagenUsuario
    },
    mounted() {
        // default mounted
    },
}
</script>
