<template>
    <v-app>
        <header_user></header_user>
        <loading v-if="this.$store.state.loading"></loading>
        <v-main class="fondohome" v-else>
            <v-container class="mt-12">
                <v-container class="mt-12"></v-container>
                <v-container class=""></v-container>
                <v-row align="center" justify="center">
                    <v-col class="text-center" cols="12">
                        <h1 class="text-h2 mb-4 white--text">
                            Utalca Search
                        </h1>
                        <h4 class="subheading white--text">
                            Busca y crea informacion para la comunidad utalina.
                        </h4>
                        <h6 class="mt-5 subheading white--text">
                            PSDT: Tranquilo, es totalmente ilegal.
                        </h6>

                        <v-row class="mt-4">
                            <v-spacer></v-spacer>
                            <v-flex xs6 sm5 md4>
                                <v-text-field v-model="buscar_text" background-color="white" height="30" rounded
                                    label="Buscar" single-line prepend-inner-icon="mdi-search-web" color="#0d1117"
                                    @keyup.enter="buscar"></v-text-field>
                            </v-flex>
                            <v-spacer></v-spacer>
                        </v-row>
                    </v-col>
                </v-row>
            </v-container>

        </v-main>
    </v-app>
</template>
<script>
import loading from "@/components/loading.vue"
import header_user from "@/components/header_user.vue"
export default {
    name: 'App',
    data: () => ({
        buscar_text: "",
    }),
    components: {
        loading,
        header_user
    },
    methods: {
        buscar() {
            this.$store.state.busqueda = this.buscar_text
            this.$store.state.loading = true
            this.$store.commit('cargar_datos')
            this.$router.push({ path: "/search" })
        }
    },
    destroyed() {
        console.log("App eliminado");
    },
    beforeMount() {
        this.$store.state.loading = true
        this.$store.commit('cargar_datos')
        //color: rgb(255 255 255/var(--tw-text-opacity));
    },
};
</script>
