<template>
    <v-app >
        <header_user></header_user>
        <v-main style="padding:0;background-color: #151f32;">
            <loading v-if="this.$store.state.loading"></loading>
            <v-container class="white--text" v-else>
                <v-container class="mt-12"></v-container>
                <v-col>
                    <v-row>
                        <v-spacer></v-spacer>
                        <h1 style="font-size: 5.2rem;font-weight: 300;font-family: Poppins,sans-serif;">Page not found</h1>
                        <v-spacer></v-spacer>
                    </v-row>
                    <v-row>
                        <v-spacer></v-spacer>
                        <v-img  src="@/assets/404.png" ></v-img>
                        <v-spacer></v-spacer>
                    </v-row>
                </v-col>
            </v-container>
        </v-main>
    </v-app>
</template>
<script>
import loading from "@/components/loading.vue"
import resultados from "@/components/resultados.vue"
import header_user from "@/components/header_user.vue"
export default {
    name: 'App',
    data: () => ({
        buscar_text: "",
        resultados:[],
    }),
    components: {
        loading,
        resultados,
        header_user
    },
    methods: {
        buscar() {
            this.$store.state.busqueda = this.buscar_text
            //conectar con la api
            this.axios.get("todos_publicaciones").then((response) => {
                this.resultados = response.data
                var buscar = this.$store.state.busqueda.toLowerCase()
                this.resultados = this.resultados.filter(r => r.titulo.toLowerCase().indexOf(buscar) !== -1)
                console.log(this.resultados)
            })
            this.$store.state.loading = true
            this.$store.commit('cargar_datos')
        }
    },
    destroyed() {
        console.log("App eliminado");
    },
    beforeMount() {
        this.$store.state.loading = true
        this.$store.commit('cargar_datos')
    },
};
</script>