<script>
import loading from "@/components/loading.vue"
import resultados from "@/components/resultados.vue"
export default {
    name: 'App',
    data: () => ({
        buscar_text: null,
        resultados: [],
        cargando: true,
        drawerMovil: false,
    }),
    components: {
        loading,
        resultados
    },
    methods: {
        buscar() {
            this.cargando = true
            if(this.buscar_text!=null){
                this.$store.state.busqueda = this.buscar_text
            }
            //conectar con la api
            this.axios.get("todos_publicaciones").then((response) => {
                const publicaciones = response.data
                if (this.$store.state.busqueda == "") {
                    this.resultados = publicaciones
                    this.$store.state.resultados = this.resultados
                } else {
                    var buscar = this.$store.state.busqueda.toLowerCase()
                    this.resultados = publicaciones.filter(r => r.titulo.toLowerCase().indexOf(buscar) !== -1)
                    this.$store.state.resultados = this.resultados
                }
                this.axios.get("todos_usuarios").then((res) => {
                    const usuarios = res.data
                    for (var i = 0; i < this.resultados.length; i++) {
                        var user_post = usuarios.filter(u => u._id == this.resultados[i].refcreador)
                        this.resultados[i].nombre = user_post[0].nombre_user
                    }
                    this.cargando = false
                })
            })
            this.$store.state.loading = true
            this.$store.commit('cargar_datos')
        },
        redirigir_home(){
            this.$router.push("/").catch(error => {
                if (error.name != "NavigationDuplicated") {
                    console.log("Navegacion Error")
                }
            });
        },
        accion_card_user(opc) {
            if (opc == 1) {
                this.$router.push("/micuenta").catch(error => {
                    if (error.name != "NavigationDuplicated") {
                        console.log("Navegacion Error")
                    }
                });
            }
            else if (opc == 2) {
                this.$router.push("/").catch(error => {
                    if (error.name != "NavigationDuplicated") {
                        console.log("Navegacion Error")
                    }
                });
                localStorage.clear()
            }
        },
        redirigir_home(){
            this.$router.push("/").catch(error => {
                if (error.name != "NavigationDuplicated") {
                    console.log("Navegacion Error")
                }
            });
        }
    },
    destroyed() {
        console.log("App eliminado");
    },
    beforeMount() {
        this.$store.state.loading = true
        this.$store.commit('cargar_datos')
    },
    created() {
        this.buscar()
    }
};
</script>
