<template>
    <v-app>
        <header_user></header_user>
        <v-main style="padding:0">
            <loading v-if="this.$store.state.loading"></loading>
            <mi_cuenta v-else></mi_cuenta>
        </v-main>
    </v-app>
</template>
<script>
import loading from "@/components/loading.vue"
import mi_cuenta from "@/components/mi_cuenta.vue"
import header_user from "@/components/header_user.vue"
export default {
    name: 'App',
    data: () => ({
        buscar_text: "",
        resultados: [],
    }),
    components: {
        loading,
        mi_cuenta,
        header_user
    },
    methods: {
        buscar() {
            this.$store.state.loading = true
            this.$store.state.busqueda = this.buscar_text
            this.$router.push({ path: '/search' })
            this.$store.commit('cargar_datos')
        },

    },
    destroyed() {
        console.log("App eliminado");
    },
    beforeMount() {
        this.$store.state.loading = true
        this.$store.commit('cargar_datos')
    },
};
</script>